import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)
// const baseUrl='/pcsheckwebdev'
const baseUrl=''
  const routes = [
  {
    path:baseUrl+ '/',
    name: 'Home',
    component: () => import('@/pages/home.vue')
  },
  {
    path:baseUrl+ '/howDoWeDoIt',
    name: 'Como lo hacemos',
    component: () => import('@/pages/howDoWeDoIt.vue')
  },
  {
    path:baseUrl+ '/aboutUs',
    name: 'Nosotros',
    component: () => import('@/pages/aboutUs.vue')
  },
  {
    path:baseUrl+ '/projects',
    name: 'Proyectos',
    component: () => import('@/pages/projects.vue')
  },
  {
    path:baseUrl+ '/donations',
    name: 'Donaciones',
    component: () => import('@/pages/donations.vue')
  },
  {
    path:baseUrl+ '/museum',
    name: 'Museo',
    component: () => import('@/pages/museum.vue')
  },
  {
    path:baseUrl+ '/contact',
    name: 'Contacto',
    component: () => import('@/pages/contact.vue')
  },
  {
    path:baseUrl+ '/recondition',
    name: 'Reacondicionar',
    component: () => import('@/pages/recondition.vue')
  },
]

const router = new VueRouter({
  base: baseUrl,
  mode: 'hash',
  routes
})

export default router
